s que puedan flotar. Los componentes principales incluyen una estructura metálica, una entrada, válvulas de muestreo, válvulas de inyección, tuberías y una salida. Las tuberías están configuradas de manera ascendente y serpenteante para reducir la cantidad de espacio necesario para brindar un tiempo de reacción química suficiente (FRC systems, 2015). ")])]),_c('div',{staticClass:"col-10 col-lg-6"},[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/images/tema2/img_15.png")}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-6"},[_c('h3',{staticClass:"mb-5"},[_vm._v("Mecanismo – funcionamiento ")]),_c('p',{staticClass:"mb-3"},[_vm._v("Lo que ocurre dentro de un floculador es un concepto bastante simple, se bombea agua hacia del floculador, inmediatamente después de la entrada se da inicio a la dosificación química, generalmente coagulantes, mediante válvulas de inyección. Más adelante se inyecta un polímero floculante y se mezcla en las secciones subsiguientes del floculador. Algunas unidades incluyen un medidor de pH en línea y puntos de inyección para agentes neutralizantes (FRC systems, 2015), ver figura 18.")])]),_c('div',{staticClass:"col-10 col-lg-6"},[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/images/tema2/img_16.png")}})])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-6"},[_c('h3',{staticClass:"mb-5"},[_vm._v("Dosificación química")]),_c('p',{staticClass:"mb-3"},[_vm._v("Se puede variar de acuerdo con el tipo de floculante, pues permite que las partículas coloidales desestabilizadas o las partículas formadas durante la etapa de coagulación se ensamblen en agregados y luego se puedan separar del medio acuoso; entre ellos se encuentran los floculantes catiónicos y los aniónicos (FRC systems, 2015).")])]),_c('div',{staticClass:"col-10 col-lg-6"},[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso