oncentración del lodo se realiza en un estanque independiente. La digestión con oxígeno puro es una modificación del proceso de digestión aerobia en el que se sustituye el aire por oxígeno puro. El lodo que resulta es parecido al lodo que se obtiene en los procesos de digestión aerobia convencionales. La digestión aerobia termófila representa un refinamiento adicional del proceso de digestión aerobia. Este proceso puede permitir conseguir altos rendimientos de eliminación de la fracción biodegradable (superiores al 80%) en tiempos de detención cortos (3 a 4 días), mediante la acción de bacterias termófilas a temperaturas entre 25º y 50º C superiores a la temperatura ambiente (Aguamarket, 2017).")])]),_c('div',{staticClass:"col-12 col-lg-4"},[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/images/tema2/img_23.png")}})])])]),_c('div',{staticClass:"tarjeta back-tab px-5 py-4",attrs:{"titulo":"Digestión aeróbica convencional"}},[_c('div',{staticClass:"row px-2 py-2 justify-content-center"},[_c('div',{staticClass:"col-lg-8"},[_c('h4',[_vm._v("Digestión aeróbica convencional ")]),_c('p',{staticClass:"mb-0"},[_vm._v("En esta el lodo se airea durante un largo período de tiempo en un estanque abierto, sin calefacción, empleando difusores convencionales o aireadores superficiales. El proceso se puede llevar a cabo de manera continua o discontinua. En plantas de pequeño tamaño se emplea el sistema discontinuo, en el que el lodo se airea y se mezcla completamente durante un largo período de tiempo, dejándose sedimentar a continuación en el interior del mismo estanque (Aguamarket, 2017), ver figura 22.")])]),_c('div',{staticClass:"col-12 col-lg-4"},[_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/images/tema2/img_22.svg")}})])])])])],1)]),_vm._m(50),_vm._m(51),_c('div',{staticClass:"row align-items-star justify-content-center mb-5",attrs:{"data-aos":"zoom-in-down"}},[_c('div',{staticClass:"col-lg-12"},[_c('TabsC',{staticClass:"color-acento-contenido"},[_c('div',{staticClass:"py-3 py-md-4 color-tab p-4 m-top-4 m-top-5",attrs:{"titulo":"¿Dónde sucede el proceso?"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/images/tema2/img_25.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-8 mb-4 mb-md-0"},[_c('h4',[_vm._v("¿Dónde sucede el proceso? ")]),_c('p',[_vm._v("La conversión biológica de la materia orgánica de los lodos parece que se produce en tres etapas. "),_c('b',[_vm._v("El primer paso del proceso comporta la transformación por vía enzimática (hidrólisis) de los compuestos de alto peso molecular en compuestos que puedan servir como fuentes de energía y de carbono celular.")]),_vm._v(" El segundo paso (acidogénesis) implica la conversión bacteriana de los compuestos producidos en la primera etapa en compuestos intermedios identificables de menor peso molecular. El tercer paso (metanogénesis) supone la conversión bacteriana de los compuestos intermedios en productos finales más simples, principalmente metano y dióxido de carbono. En un digestor, la conversión de los lodos orgánicos y de los residuos se lleva a cabo mediante la acción conjunta de diferentes organismos anaerobios (Aguamarket, 2017).")])])])]),_c('div',{staticClass:"py-3 py-md-4 color-tab p-4 m-top-4 m-top-5",attrs:{"titulo":"Etapas de la conversión biológica"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/images/tema2/img_26.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-8 mb-4 mb-md-0"},[_c('h4',[_vm._v("Etapas de la conversión biológica ")]),_c('p',{staticClass:"mb-3"},[_c('b',[_vm._v("La conversión biológica de la materia orgánica de los lodos parece que se produce en tres etapas.")]),_vm._v(" El primer paso del proceso comporta la transformación por vía enzimática (hidrólisis) de los compuestos de alto peso molecular en compuestos que puedan servir como fuentes de energía y de carbono celular. El segundo paso (acidogénesis) implica la conversión bacteriana de los compuestos producidos en la primera etapa en compuestos intermedios identificables de menor peso molecular. El tercer paso (metanogénesis) supone la conversión bacteriana de los compuestos intermedios en productos finales más simples, principalmente metano y dióxido de carbono. En un digestor, la conversión de los lodos orgánicos y de los residuos se lleva a cabo mediante la acción conjunta de diferentes organismos anaerobios (Aguamarket, 2017).")])])])]),_c('div',{staticClass:"py-3 py-md-4 color-tab p-4 m-top-4 m-top-5",attrs:{"titulo":"Hidrolización"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/images/tema2/img_27.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-8 mb-4 mb-md-0"},[_c('h4',[_vm._v("Hidrolización ")]),_c('p',{staticClass:"mb-3"},[_c('b',[_vm._v("Un grupo de microorganismos se ocupa de la hidrolización de los polímeros orgánicos y de los lípidos para formar elementos estructurales básicos como los monosacáridos, los aminoácidos y los compuestos relacionados con estos.")]),_vm._v(" Un segundo grupo de bacterias anaerobias fermenta los productos de la descomposición para producir ácidos orgánicos simples, de los que el que se presenta con mayor frecuencia en los digestores orgánicos es el ácido acético. Este grupo de microorganismos, que reciben el nombre de no metanogénicos, está formado por bacterias facultativas y anaerobias estrictas, aunque de forma colectiva se conocen como bacterias \"formadoras de ácidos (Aguamarket, 2017).")])])])])])],1)]),_vm._m(52),_vm._m(53),_vm._m(54),_vm._m(55),_vm._m(56),_vm._m(57),_vm._m(58),_c('p',{staticClass:"mb-3"},[_vm._v("Los contactores biológicos rotativos están basados en los procesos biológicos aerobios. Estos requieren un contacto íntimo entre el agua residual, la biomasa activa y el oxígeno. Consisten en una serie de placas o discos colocados en un eje horizontal que giran lentamente dentro del tanque que contiene el agua residual, con un 49% de la superficie sumergida. Sobre la superficie de los discos se fija la biomasa que se encuentra alternativamente en contacto con el agua residual y el aire. Esto posibilita la ingestión de la materia orgánica por parte de los microorganismos y su posterior degradación en presencia del oxígeno del aire (Cyclusid, 2020), ver figura 28. ")]),_vm._m(59),_vm._m(60),_vm._m(61),_c('div',{staticClass:"row justify-content-evenly align-items-star p-2"},[_c('div',{staticClass:"col-lg-12",attrs:{"data-aos":"fade-right"}},[_c('AcordionA',{attrs:{"tipo":"a","clase-tarjeta":"tarjeta tarjeta--gris","data-aos":"zoom-in-down"}},[_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Los biocilindros"}},[_c('div',{staticClass:"col-md-2 mb-4 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/images/tema2/img_new_4.svg")}})]),_c('div',{staticClass:"col-md-10 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-0"},[_vm._v(" Son una variante de los biodiscos, su principal diferencia es la sustitución de los discos por biocilindros, están formados por una malla metálica, cuyo interior se rellena de piezas de plástico que sirven de soporte a la biomasa. En estos sistemas se produce un exceso de biomasa que se desprende del contactor, esta se mantiene en suspensión en el tanque gracias al movimiento del material soporte. Para regular la cantidad de microorganismos presentes en el sistema se dispone de un clarificador o decantador secundario, en él se produce la sedimentación del exceso de biomasa (Cyclusid, 2020), ver figura 29.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Características notables del proceso con biodiscos"}},[_c('div',{staticClass:"col-md-2 mb-4 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/images/tema2/img_new_5.svg")}})]),_c('div',{staticClass:"col-md-10 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-3"},[_vm._v(" Notable elasticidad, es decir, es capaz de absorber elevadas puntas de carga orgánica e hidráulica, manteniendo muy estables los niveles de salida. No requiere recirculación de fangos; el fango activo del proceso es el que permanece adherido a los discos. Si la carga aumenta, la película bacteriana crece espontáneamente y los sólidos en suspensión a la salida del biodisco disminuyen. Si la carga disminuye, la biomasa se desprende y aumentan los sólidos en el efluente al decantador secundario. No es necesario controlar la concentración del licor de mezcla, ya que el fango extraído es un desecho del proceso y ha de eliminarse como mejor convenga.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Ruidos y otras variables"}},[_c('div',{staticClass:"col-md-2 mb-4 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/images/tema2/img_new_6.svg")}})]),_c('div',{staticClass:"col-md-10 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-3"},[_vm._v(" El nivel de ruidos es muy bajo, factor que es muy importante tanto ambientalmente como por higiene y salud del personal. Las dimensiones de los depósitos de oxidación son menores que los utilizados por otros procesos. El rendimiento del proceso es más estable en épocas frías, debido al hecho de permanecer cubiertos los tanques. El consumo energético es muy reducido y a igualdad de resultados, comparándolo con un sistema de fangos activos, el consumo resulta ser la tercera parte Hidrometalica (s.f.).")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"El funcionamiento de los biodiscos"}},[_c('div',{staticClass:"col-md-2 mb-4 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/images/tema2/img_new_7.svg")}})]),_c('div',{staticClass:"col-md-10 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-3"},[_vm._v(" Se divide en cuatro etapas, ver figura 30.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"El agua bruta"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-3"},[_vm._v(" El agua bruta de llegada entra a este tanque a través de la tubuladura de entrada, donde se produce una primera sedimentación de materia sólida. Los sólidos sedimentados se extraerán periódicamente por parte del usuario en la primera etapa donde se realiza la primera toma de contacto entre el agua bruta de entrada y los discos. La superficie de los discos se coloniza de forma natural, formando un recubrimiento visible. En la segunda etapa de contacto, gracias a la concepción del sistema de vertedero entre ambas etapas, se consigue un nivel de agua con menos fluctuaciones, favoreciendo el proceso y su salida hacia el “tanque de sedimentación secundaria”. Con el proceso de dos etapas se consigue aumentar el crecimiento de la biomasa de forma equilibrada y constante. En el tanque de sedimentación secundaria se produce la sedimentación de la materia sólida restante del proceso, a la cual se le realizan dos operaciones, la recirculación y extracción de sedimentos (Hidrometálica, s.f.). ")])])])])],1)]),_vm._m(62),_c('p',{staticClass:"mb-5"},[_vm._v("Son reactores discontinuos en los que el agua residual se mezcla con un lodo biológico en un medio aireado. Se trata de un proceso que se combina en un mismo tanque: aeración/reacción y clarificación. La tecnología de los reactores biológicos secuenciales es una variante optimizada de la tecnología convencional de lodos activados. Se basa en el uso de un solo reactor que opera en forma discontinua secuencial. El sistema de los reactores biológicos secuenciales SBR consta de al menos cuatro procesos cíclicos: llenado, aireación, anoxia, decantación y vaciado, tanto de efluente como de los lodos. Esta tecnología es capaz de tolerar variaciones de carga y caudal y genera como producto lodos estabilizados, siendo en ocas