ma es el inductor especial: el aireador impulsor gira creando un remolino y una depresión dentro de la celda que produce una aspiración del aire a través del eje hueco. El aire penetra en el líquido al llegar a la celda de aireación, mediante grandes cantidades de micro burbujas. La mezcla rica en aire es liberada en la celda de flotación facilitando la separación de fases (Aguas Sigma, 2016).")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-3",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"jumbotron-1 p-2"},[_c('p',{staticClass:"text-white text-center mb-0"},[_c('b',[_vm._v("Coagulación-floculación.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-star mb-5"},[_c('div',{staticClass:"col-lg-5 mb-3",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"jumbotron p-5 h-100"},[_c('p',{staticClass:"mb-3"},[_vm._v("En muchos casos parte de la materia en suspensión está formada por partículas de muy pequeño tamaño, lo que conforma una suspensión coloidal. Estas suspensiones coloidales suelen ser muy estables, en muchas ocasiones debido a interacciones eléctricas entre las partículas. Por tanto, tienen una velocidad de sedimentación extremadamente lenta, por lo que haría inviable un tratamiento mecánico clásico. Una forma de mejorar la eficacia de todos los sistemas de eliminación de materia en suspensión es la adición de ciertos reactivos químicos que, en primer lugar, desestabilicen la suspensión coloidal (coagulación) y a continuación favorezcan la floculación de estas para obtener partículas fácilmente sedimentables (Acuatecnia S.A.S., 2018), ver figura 17.")])])]),_c('div',{staticClass:"col-12 col-lg-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"titulo-sexto color-acento-contenido mb-3"},[_c('h5',[_vm._v("Figura