te, suelen emplearse dos variantes del proceso de digestión aerobia: el sistema convencional y el sistema con oxígeno puro, aunque también se ha empleado la digestión aerobia termófila (Aguamarket, 2017), ver figura 21.")])])]),_c('div',{staticClass:"col-12 col-lg-6",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"titulo-sexto color-acento-contenido mb-3"},[_c('h5',[_vm._v("Figura 21")]),_c('span',[_vm._v("Tratamiento aerobio")])]),_c('img',{staticClass:"mb-3",attrs:{"src":require("@/assets/curso/images/tema2/img_new_2.svg")}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-2",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"jumbotron-1 p-2"},[_c('p',{staticClass:"text-white text-center mb-0"},[_c('b',[_vm._v("Digestor anaerobio.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-star mb-5"},[_c('div',{staticClass:"col-lg-6 mb-3",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"jumbotron p-5 h-100"},[_c('p',{staticClass:"mb-3"},[_vm._v("En este proceso se produce la descomposición de la materia orgánica e inorgánica en ausencia de oxígeno molecular. Sus principales aplicaciones han sido y siguen siendo hoy en día, la estabilización de lodos concentrados producidos en el tratamiento del agua residual y de determinados residuos industriales; sin embargo, recientemente se ha demostrado que los residuos orgánicos diluidos también se pueden tratar anaeróbicamente. En el proceso de digestión anaerobia, la materia orgánica contenida en la mezcla de lodos primarios y biológicos se convierte biológicamente, bajo condiciones anaerobias, en metano (CH4) y dióxido de carbono (C02) (Aguamarket, 2017), ver figura 23")])])]),_c('div',{staticClass:"col-12 col-lg-6",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"titulo-sext